import React, { useState, useEffect } from 'react';
import Classes from './LoaderDesk.module.css';
import List from '../components/List';

const LoaderDesk = () => {
    const [seconds, setSeconds] = useState(0);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const interval = setInterval(() => {
            setSeconds(seconds => seconds + 1);
            setLoading(seconds => false);
        }, 5000);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="text-uppercase font-weight-bolder">
            <List isLoading={loading} />
            <header className={[Classes.Paragraph, "d-flex align-items-center"].join(' ')}>
                <span><strong>{seconds}&nbsp;</strong>seconds have elapsed since mounting.</span>
            </header>
        </div>
    );
};

export default LoaderDesk;